<template>
  <v-layout row wrap id="content">
    <v-flex xs12>      
      <h2>Сделайте рассчет индивидуальной сауны</h2>  
    </v-flex>
    <v-flex xs12 class="calculator">
      <div class="select">
        <div class="saun">
          <p>Выберите класс сауны:</p>
          <select v-model="value_class" name="class_saun" id="class-saun">
            <option disabled>Класс сауны</option>
            <option v-for="item in class_saun" :key="item.id" :value="item.id">{{ item.class }}</option>
          </select>
          <div class="select-arrow"></div>
        </div>
        <div class="interior-trim">
          <p>Внутренняя обшивка:</p>
          <select v-model="value_interior_trim" name="interior_trim" id="interior_trim">
            <option disabled>Тип дерева</option>
            <option v-for="item in interior_trim" :key="item.coefficient" :value="item.coefficient">{{ item.tree_type }}</option>
          </select>
          <div class="select-arrow"></div>
        </div>
      </div>
      <div class="size">
        <p>Размеры:</p>
        <div>
          <input v-model="width" type="number" placeholder="Ширина" class="input">
          <p>Ширина, см</p>
        </div>
        <div>
          <input v-model="length" type="number" placeholder="Ширина" class="input">
          <p>Длина, см</p>
        </div>
        <div>
          <input v-model="height" type="number" placeholder="Ширина" class="input">
          <p>Высота, см</p>
        </div>
      </div>
      <div class="people-lamp">
        <p>Вместимость: {{ counter_person }}</p>
        <div class="slider person">
          <button @click="reductionPeople"><img src="../assets/minus_icon-icons.com_70037.png" alt="minus"></button>
          <input v-model="person" type="range" min="1" max="10" step="1" >
          <button @click="addingPeople"><img src="../assets/plus_icon-icons.com_69985.png" alt="plus"></button>
        </div>
        <p>Освещение: {{ luminaire_counter }}</p>
        <div class="slider lamp">
          <button @click="reductionLamp"><img src="../assets/minus_icon-icons.com_70037.png" alt="minus"></button>
          <input v-model="lamp" type="range" min="1" max="10" step="1">
          <button @click="addingLamp"><img src="../assets/plus_icon-icons.com_69985.png" alt="plus"></button>
        </div>
      </div>
    </v-flex>
    <v-flex xs12>
      <div class="calculation-result">
        <div class="calculation-data">
          <p>Внутренняя обшивка: {{ tree_type }}</p>
          <p>Размер: {{width}}х{{length}}х{{height}} </p>
          <p>Вместимость: {{ counter_person }}</p>
          <p>Освещение: {{ luminaire_counter }}</p>
        </div>
        <div class="registration-form">
          <form>
            <input v-model="customer_name" type="text" placeholder="Имя">
            <input v-model="customer_phone" type="phone" placeholder="Телефон">
          </form>
          <button @click="calculationResult">Получить точный рассчет</button>
        </div>
      </div>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'calculator',
  data() {
    return {
      value_class: 'Класс сауны',
      value_interior_trim: 'Тип дерева',
      tree_type: '',
      width: null,
      length: null,
      height: null,
      person: 1,
      lamp: 1,
      class_saun: [
        { id: 0,
          class: 'Люкс',
          square: { '5-8': 7200, '9-12': 7050, '13-20': 6900 }     
        },
        { id: 1,
          class: 'Премиум',  
          square: { '5-8': 6300, '9-12': 6070, '13-20': 5840 }
        },
        { id: 2,
          class: 'Стадарт', 
          square: { '5-8': 5200, '9-12': 5050, '13-20': 5900 }
        },
        { id: 3,
          class: 'Эконом',
          square: { '5-8': 3310, '9-12': 3120, '13-20': 2930 }
        },
      ],
      interior_trim: [
        { tree_type: 'Липа',  coefficient: 2.5 },
        { tree_type: 'Ольха',  coefficient: 3 },
        { tree_type: 'Кедр',  coefficient: 4.2 },
      ],
      customer_name: null,
      customer_phone: null,
    }
  },
  computed: {
    sauna_square: {
      get() {
        return Math.round((this.width / 100) * (this.length / 100) * 100) / 100;
      }
    },
    sauna_cost: {
      get() {
        let cost = null;
        if(this.value_class === 0) {
           cost = this.getCostSaune(this.sauna_square);      
        } else if(this.value_class === 1) {
          cost = this.getCostSaune(this.sauna_square);
        } else if(this.value_class === 2) {
          cost = this.getCostSaune(this.sauna_square);
        } else if(this.value_class === 3) {
          cost = this.getCostSaune(this.sauna_square);
        } 
        return cost;
      }
    },
    counter_person: {
      get() {
        return (this.person > 1 && this.person < 5) ? this.person + ' человека' : this.person +  ' человек';
      }
    },
    luminaire_counter: {
      get() {
        if (this.lamp === 1) { 
          return this.lamp + ' светильник' 
          } else if (this.lamp > 1 && this.lamp < 5) {
            return this.lamp +  ' светильника'
          } else {
            return this.lamp +  ' светильников' 
          }
      }
    }
  },
  methods: {
    getCostSaune(val) {
      let cost = null;
      if(val < 9) {
        cost = this.class_saun[this.value_class].square["5-8"];
      } else if(val >= 9 && val < 13) {
        cost = this.class_saun[this.value_class].square["9-12"];
      } else if(val >= 13) {
        cost = this.class_saun[this.value_class].square["13-20"];
      };
      return cost;
    },
    addingPeople() {
      return (this.person >= 1 && this.person < 10) ? this.person++ : this.person = 10
    },
    reductionPeople() {
      return (this.person > 1 && this.person <= 10) ? this.person-- : this.person = 1
    },
    addingLamp(){
      return (this.lamp >= 1 && this.lamp < 10) ? this.lamp++ : this.lamp = 10
    },
    reductionLamp(){
      return (this.lamp > 1 && this.lamp <= 10) ? this.lamp-- : this.lamp = 1
    },
    calculationResult() {
      let summ = null;
      const cost_lamp = (this.lamp > 5) ? 1500 : 0;
      const cost_person = (this.person > 3) ? 2000 : 0;
      summ = this.sauna_square * this.sauna_cost * this.value_interior_trim + cost_lamp + cost_person;
      this.$store.commit('saveSumm', summ);
      if(this.sauna_cost) {
        this.$router.push('/result');
      } else {
        alert ('Вы не выбрали все поля');
      }      
    }
  },
  watch: {
    value_interior_trim(value) {
      this.interior_trim.map(item => {
       if (item.coefficient === this.value_interior_trim) {
         return this.tree_type = item.tree_type;
       }
      })      
    }
  }
}
</script>


